@import url(https://fonts.googleapis.com/css?family=Caveat);

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: hp;
  src: url(/API-harryPotter-Quiz/static/media/HARRYP__.181ef9a7.TTF);
}

@font-face {
  font-family: hp2;
  src: url(/API-harryPotter-Quiz/static/media/LumosLatino.80f6d244.ttf);
}

div.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  color: #222;
}

header {
  -webkit-flex-basis: 40vh;
          flex-basis: 40vh;
  font-family: hp, sans-serif;
}



main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  font-family: 'Caveat', cursive;
}


section {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  background-image: url(/API-harryPotter-Quiz/static/media/771px-DHhogwarts.80a88297.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  position: relative;
  width: 100vw;
}


footer {
  background-color: #000;
  color: white;
  font-size: .8rem;
  text-align: right;
  padding: 1px 1vw;

}

.title {
  text-align: center;
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
}

.title h2 {
  text-transform: uppercase
}
header {
    overflow: hidden;
    position: relative;
    height: 30vh;
}

header img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-height: 100%;
    min-width: 100%;
}

h1 {
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    color: #ddd;
    letter-spacing: 4px;
    text-transform: uppercase;
    width: 100vw;
    text-align: center;
    background-color: rgba(0, 0, 0, .5);
    padding: 2vh;
}

nav {
    position: absolute;
    width: 100vw;
    bottom: 0;
    background-color: rgba(255, 255, 255, .7);
}
.nav ul {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    width: 100vw;
    -webkit-align-items: stretch;
            align-items: stretch;
    font-family: hp2, sans-serif;
    font-size: 1.3rem;
    border-bottom: 2px solid #000;
}

.nav li {
    -webkit-flex-grow: 1;
            flex-grow: 1;
}


.nav a {
    display: block;
    /* width: 10vw; */
    text-align: center;
    text-decoration: none;
    color: black;
    background-color: transparent;
    height: 100%;
    padding: 2vh 0;
    font-weight: bold;
    border-left: 2px solid #111;
    -webkit-transition: .2s;
    transition: .2s;
}

.nav li:nth-child(1) a {
    border: none;
}

.nav a:hover {
    background-color: rgba(0, 0, 0, .5);
}
.infoPage {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, .8);
    padding: 3vw;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
}




.search {
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
}

.search label {
    font-size: 1.5rem;
}

.search input {
    margin: 0 25px;
    padding: 5px;
    width: 55%;
    background-color: transparent;
    color: white;
    border: 2px solid white;
    font-size: 1.2rem;
    border-radius: 5px;

}

.search button {
    padding: 5px 30px;
    background-color: #eee;
    border: 2px solid white;
    border-radius: 5px;
    text-transform: uppercase;
    font-family: Caveat, sans-serif;
    font-weight: bold;
    font-size: 1.1rem;
    -webkit-transition: .4s;
    transition: .4s;
    cursor: pointer;
}

.search button:hover {
    background-color: transparent;
    color: white;
}

.answer {
    margin-top: 5vh;
    text-align: center;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.answer h4 {
    text-transform: uppercase;
    font-size: 2.5rem;
    margin-bottom: 2vh;
}

.answer p {
    padding: 1vh 0;
    font-size: 1.5rem;
}
.housePage {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, .6);
    padding: 3vw;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    scrollbar-track-color: red;
}

.buttons-house {
    display: -webkit-flex;
    display: flex;
    width: 80vw;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 20vh;
    /* background-color: #aaa; */
}


.button-house {
    position: relative;
    cursor: pointer;

}

.button-house p {
    top: 10%;
    position: absolute;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    text-shadow: 2px 2px 1px black;
    font-size: 1.1rem;
}

.button-house img {
    top: -15%;
    left: 10%;
    position: absolute;
    height: 20vh;
}

.button-house:not(:hover) {
    opacity: .7
}

.housePage ul {
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    overflow-y: scroll;
    height: 20vh;
}

.housePage li {
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
    font-size: 1.1rem;
}
.gameHousePage {
    position: relative;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    text-align: center;
    height: 100%;
    font-size: 1.7rem;
    background-color: rgba(0, 0, 0, .6);
}

.gameHousePage button {
    background-color: transparent;
    border: 3px solid white;
    border-radius: 5px;
    color: white;
    padding: 1vh 10vw;
    font-size: 1.7rem;
    font-family: caveat, sans-serif;
    cursor: pointer;
    -webkit-transition: .3s;
    transition: .3s;
}

.gameHousePage button:hover {
    background-color: rgba(255, 255, 255, .9);
    color: black;
}

.gameHousePage .result {
    position: absolute;
    top: 10%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    text-transform: uppercase;
    z-index: 10;
    font-size: 3rem;
    background-color: black;
    border-radius: 10px;
    padding: 10vh 0;
    width: 80%;
    border: 5px solid white;
}
.listPage {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, .6);
    padding: 3vw;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    scrollbar-track-color: red;
}

.listPage ul {
    list-style: none;
    text-align: left;
    font-size: 1.3rem;
    font-weight: lighter
}

.listPage li {
    margin-bottom: 1vh;
    letter-spacing: 1px;
}

.listPage .answer {
    overflow-y: scroll;
    height: 30vh;

}
.homePage {
    color: white;
    position: relative;
    height: 100%;
    width: 100%;
}

.title-home {
    position: absolute;
    bottom: 0;
    right: 0;
    text-align: right;
    padding: 3vw;
}

.title-home h2 {
    font-size: 3rem;
    text-transform: uppercase;
    margin-bottom: 6vh;
}

.title-home p {
    font-size: 1.4rem;
    margin-bottom: .5vh;
}
